@import url(https://fonts.googleapis.com/css?family=Barlow:400,700,900&display=swap);
/* global styles */

body {
  font-family: 'Barlow', sans-serif;
  background-color: #efefef;
  overflow: hidden;
}

.bg-black-transparent {
  background-color: rgba(0, 0, 0, 0.5);
}

.hero-slide {
  height: 90vh !important;
  display: flex;
  position: relative;
  overflow: hidden;
}
.slide-image {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero-slider .content {
  position: relative;
  z-index: 2;
}

.image{
  height: 90vh;
  width: auto;
}

